export default {
	data() {
   	return {
		   id: 0,
         tab: [
            {
               link: '',
               title: '行业知识'
            },
            {
               link: '',
               title: '公司新闻'
            },
         ],
         tabs: true,
         info: [
         ]
   	}
	},
   created (){
      this.id = this.$route.query.id;
      if (this.tab.length <= 2) {
         this.tabs = true;
      }else{
         this.tabs = false;
      }
   }
}




